<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process263"></a>

<p><br></p>
<p><span class="rvts17">矮人的秘密（</span><span class="rvts50">The Secrets of the Dwarves</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:404,Y:624</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：伊泽贝尔（</span><span class="rvts20">Isbeil</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">发现矮人的秘密，打败伊泽贝尔，并选择是否杀掉矮人女王</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1446.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">从阿克斯的下水道，可以通过一路解锁，来到图上的区域，发现有可以触发墙面打开的机关。使用机关，接下来将会遇到矮人女王和伊泽贝尔。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1447.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">伊泽贝尔会非常华丽的赞美你在游戏中的所有伟大壮举，像什么杀了黑环瘦黄男人，除掉了亚历山大，解放了源力束缚者等。。但是最终，她会突然一黑脸，说你是个十恶不赦的人，必须除掉。</span>
<br><span class="rvts21">矮人女王听后会感觉异常惊讶和满脑子雾水，其实她还不知道身边的这个伊泽贝尔并不是她的矮人心腹，而是一个地地道道的黑环不死矮人。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1448.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">接着所有人都被击晕，一场行刑开始了，放毒，放火，放电后，队伍的所有角色都挺了过来，这使得伊泽贝尔感到十分意外，一场战斗在所难免。</span>
<br><span class="rvts21">注意在这场战斗中，队伍刚开始会处于较为被动的地形劣势，你可以选择飞出坑或跳出坑与他们对抗，或在坑中进行祝福，得到较好的掩护效果。</span>
<br><span class="rvts21">这场战斗的难度并不大，然而对面的所有敌人都是不死族，因此它们对毒元素是免疫的。注意观察敌人的抗性，使用它们抗性较弱的技能进行攻击会得到更好的效果。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1449.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">当伊泽贝尔和所有她的手下都被干掉后，可以发现在她原来站着的地方有一个</span><span class="rvts20"></span><span class="rvts21">世界末日阀门</span><span class="rvts20"></span><span class="rvts21">，千万不要激活它！这会使整个阿克斯城都沉浸在死亡迷雾之中，所有的人都会死掉！</span>
<br><span class="rvts21">并且如果队伍中有伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹，当你激活末日阀门后，他会选择与你永久敌对。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1450.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">杀掉伊泽贝尔后，记得从左边的隐藏墙壁处逃脱。右边的隐藏墙壁是个陷阱，会喷出死亡毒雾。此时，你会遇到矮人女王贾斯汀娜，一段对话后，选择杀掉她或不理会她。</span>
<br><span class="rvts21">注意，在左边隐藏墙壁处附近有死亡烟雾木桶，如果在与伊泽贝尔的战斗中，这个木桶被击破并释放处死亡烟雾，那么矮人女王将不会出现在这里。她会出现在林德</span><span class="rvts20"></span><span class="rvts21">凯姆的家中（在二楼林德</span><span class="rvts20"></span><span class="rvts21">凯姆的房间内），与她交谈可以触发同样的剧情。（会有队友旁白提示</span><span class="rvts20"></span><span class="rvts21">矮人女王逃跑了</span><span class="rvts20"></span><span class="rvts21">）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



